* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

.header {
  width: 100%;
  height: 100px;
  height: 50px;
  background-color: #1e1e1ffc;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 17px;
  display: flex;
  position: fixed;
}

.header__logo-home {
  text-decoration: none;
}

.header-name {
  color: #f5f5f5;
  text-shadow: -3px 3px 3px #000;
  font-family: Orbitron, sans-serif;
  font-size: 30px;
}

.header__container-link {
  display: none;
}

@media (min-width: 500px) {
  .header__container-link {
    align-items: center;
    display: flex;
  }
}

.header__links {
  color: #fff;
  margin-right: 29px;
  padding: 6px;
  font-family: Poppins, sans-serif;
  font-weight: 400;
  text-decoration: none;
}

.header__container-link a:hover {
  background-color: teal;
  border-radius: 5px;
  transition: all .55s;
}

#navMenu > span {
  width: 28px;
  height: 2px;
  background-color: #fbfbfb;
  border-radius: 9999px;
  display: block;
}

#navMenu > span:not(:last-child) {
  filter: drop-shadow(2px 0 1px #000);
  margin-bottom: 7px;
}

#navMenu, #navMenu > span {
  width: 28px;
  filter: drop-shadow(1px 1px 3px #000);
  transition: all .4s ease-in-out;
}

#navMenu.active {
  transition-delay: .8s;
  transform: rotate(45deg);
}

#navMenu.active > span:nth-child(2) {
  width: 0;
}

#navMenu.active > span:nth-child(1), #navMenu.active > span:nth-child(3) {
  transition-delay: .4s;
}

#navMenu.active > span:nth-child(1) {
  transform: translateY(9px);
}

#navMenu.active > span:nth-child(3) {
  transform: translateY(-9px)rotate(90deg);
}

@media (min-width: 500px) {
  #navMenu {
    display: none;
  }
}

.button-open-img {
  height: 35px;
  width: 30px;
}

.header__ventana-burger {
  background-color: #323b35;
  padding: 20px;
  display: none;
  position: absolute;
  inset: 20px 0 0;
}

.header__ventana-burger-content {
  width: 100%;
  height: 681px;
  background-color: #417670;
  flex-direction: column;
  align-items: center;
  padding: 5px 21px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 8px;
}

@media (min-width: 500px) {
  .header__ventana-burger-content {
    display: none;
  }
}

.ventana__close {
  width: 50px;
  height: 50px;
  background-color: #0000;
  border: none;
  align-self: flex-end;
}

.img-close {
  height: 31px;
  width: 30px;
  color: #000;
}

.header__ventana-links {
  flex-direction: column;
  padding: 70px;
  display: flex;
}

.header__ventana-links a:hover {
  background-color: #02adad;
  border-radius: 7px;
  transition: all .5s;
}

.link {
  color: #000;
  border: 2px solid #19171708;
  border-radius: 60px;
  margin-bottom: 56px;
  padding: 24px;
  font-family: Montserrat, sans-serif;
  font-size: 32px;
  text-decoration: none;
  box-shadow: 11px 11px 14px #100f0fad;
}

